@font-face {
	font-family: "Flaticon";
	src: url(../dist/5ffb1d8150330fdd128631feb7a91e38.eot);
	src: url(../dist/5ffb1d8150330fdd128631feb7a91e38.eot#iefix) format("embedded-opentype"),
	url(data:application/font-woff;base64,) format("woff"),
	url(../dist/ef9ae3ed003a081e9021234bc9996c16.ttf) format("truetype"),
	url(../dist/37ab10e43d0fb951de7cd4f6316f1935.svg) format("svg");
	font-weight: normal;
	font-style: normal;
}

[class^="flaticon-"]:before, [class*=" flaticon-"]:before,
[class^="flaticon-"]:after, [class*=" flaticon-"]:after {   
	font-family: Flaticon;
    font-size: 20px;
	font-style: normal;
    display: inline-block;
    margin-top: -1px;
}

.flaticon-factory:before {
	content: "\E000";
}
