@font-face {
	font-family: "FlaticonExcel";
	src: url(../dist/2fd3cafa3a0a98e7f27161d5c3bdba56.eot);
	src: url(../dist/2fd3cafa3a0a98e7f27161d5c3bdba56.eot#iefix) format("embedded-opentype"),
	     url(data:application/font-woff;base64,) format("woff"),
         url(data:application/font-woff;base64,) format("woff"),
	     url(../dist/fa626759a7d216133c4ebc72a7d65868.ttf) format("truetype"),
	     url(../dist/c73e91a48f129da8d331f78f71f3b0ba.svg) format("svg");
	font-weight: normal;
	font-style: normal;
}

[class^="flaticonExcel-"]:before, [class*=" flaticonExcel-"]:before,
[class^="flaticonExcel-"]:after, [class*=" flaticonExcel-"]:after {   
	font-family: FlaticonExcel;
    font-size: 16px;
	font-style: normal;
    display: inline-block;
    margin-top: -1px;
}

.flaticonExcel-excel:before {
	content: "\E001";
    font-size: 12.7px;
}
